$output-bourbon-deprecation-warnings: false !default;
@import "../../bourbon/bourbon";
@import "libs";
@import "vars";
@import "settings";

.main-head {
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 80;
  background-color: $white;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, .095);
  padding: 8px 16px;
  overflow: hidden;
  box-sizing: border-box;
  .top-head {
    text-align: center; } }

aside {
  background-color: $white;
  width: 350px;
  position: fixed;
  top: 55px;
  left: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23); }

.hamburger {
  background-color: transparent;
  border: 0;
  width: 40px;
  height: 40px;
  font-size: 30px;
  outline: none;
  color: #9f9f9f;
  cursor: pointer;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  &:active {
    background-color: rgba(0, 0, 0, .1); } }

.slide-transition {
  transition: all .3s cubic-bezier(.65, .05, .36, 1); }

.slide-enter, .slide-leave {
  left: -100%; }



.training, .jobs {
  padding-left: 350px; }

.heading {
  text-align: center;
  margin-right: 350px; }

.flex-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.s-works {
  h3 {
    margin-top: 50px;
    margin-bottom: 25px; }
  .training {
    margin: 80px 0; }
  .portfolio {
    &-item {
      position: relative;
      display: block;
      overflow: hidden;
      cursor: pointer;
      img {
        @include mt(.5s);
        width: 450px;
        height: 300px;
        object-fit: contain;
        display: inline-block; }
      &:hover {
        .portfolio-item-cont {
          opacity: 1;
          transform: scale(.95); }
        img {
          transform: scale(1.1); } }
      &-cont {
        position: absolute;
        color: $color;
        padding: 20% 15px 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($white, .75);
        text-align: center;
        opacity: 0;
        @include mt(.5s);
        h3 {
          margin-bottom: 15px; }
        p {
          margin-top: 15px;
          font-size: em(20px);
          font-family: "Open Sans Light", sans-serif; }
        .fa {
          color: rgba($color, .6);
          font-size: 46px; } } } } }


.portfolio-popup {
  position: relative;
  background-color: #fff;
  display: block;
  max-width: 1000px;
  margin: 0 auto 40px;
  text-align: center;
  padding-bottom: 8px;
  @include mt(.25s);
  h4 {
    line-height: em(21px);
    margin: 20px auto;
    width: 70%; }
  &-text {
    display: block;
    width: 90%;
    margin: 10px auto;
    .fa {
      position: relative;
      top: 11px;
      font-size: 45px;
      margin-right: 10px; } } }

.s-contacts {
  background-color: lighten($color, 20%);
  color: $white;
  padding-top: 60px; }
